:root {
  /* COLORS */
  --white: #fff;
  --gray: #333;
  --blue: #1733ea;
  --lightblue: #1733ea;
  --primary: #2980B9;
  --primary-light: #2980b926;
  --ternary: #a3aed0;
  --black: #000;
  --gray1: #333;
  --gray2: #4f4f4f;
  --gray3: #828282;
  --gray4: #bdbdbd;
  --gray5: #e0e0e0;
  --gray6: #f2f2f2;
  --yellow: #f2c94c;
  --green: #27ae60;
  --success: rgba(39, 174, 96, 1);
  --success-20: rgba(39, 174, 96, 0.2);
  --success-25: rgba(39, 174, 96, 0.25);
  --danger: rgba(235, 87, 87, 1);
  --danger-20: rgba(235, 87, 87, 0.2);
  --danger-25: rgba(235, 87, 87, 0.25);
  /* RADII */
  --button-radius: 0.7rem;
  /* SIZES */
  --max-width: 768px;
  --max-height: 420px;
  --ff-montserrat: "Montserrat", sans-serif;
  --ff-poppins: "Poppins", sans-serif;
  --ff-nunito: "Nunito", sans-serif;
  --ff-raleway: "Raleway", sans-serif;
  font-size: 16px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#root {
  width: 100vw;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: #333;
  background-color: var(--gray);
  font-family: "Raleway", sans-serif;
  font-family: var(--ff-raleway);
}

/* Scrollbar style sopported only in chrome */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #e0e0e0;
  background: var(--gray5); 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #bdbdbd;
  background: var(--gray4); 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #828282;
  background: var(--gray3); 
}

