.input_container {
  display: grid;
  grid-template-areas: "select";
  align-items: center;
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 0.25em;
  padding: 0.25em 0.5em;
  font-size: 0.8rem;
  align-self: center;
  cursor: pointer;
  line-height: 1.1;
}

.input_container.light {
  background-color: var(--white);
}
.input_container.dark {
  background-color: var(--gray2);
}

.input_container.light .input{
  color: var(--gray2);
}
.input_container.dark .input{
  color: var(--white);
}

.input {
  position: relative;
  width: 100%;
  height: 100%;
  font-size: 1.5rem;
  padding: 0.5rem 0;
  border: none;
  outline: none;
  background-color: transparent;
}

.input::placeholder {
  font-size: 1.5rem;
  color: var(--gray4);
}

.input:focus + .input_focus {
  position: absolute;
  /* top: -1px; */
  left: 0;
  right: 0;
  bottom: 1px;
  border: 0.5px solid #000;
  border-radius: inherit;
}
